body, input, button {
  font-family: "Helios", sans-serif;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #000;
}

h1 {
  font-size: 1.85rem;
  margin-bottom: 3.5rem;
}

.btn {
  cursor: pointer;
  display: block;
  background: lightgray;
  font-size: 1rem;
  font-weight: 700;
  border: none;
  border-radius: 0.5rem;
  padding: 1rem 3rem;
}

header {
  position: absolute;
  width: 100%;
}
header .wrapper {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
@media (max-width: 1023px) {
  header .wrapper {
    justify-content: space-between;
  }
}
header .wrapper .logo {
  flex-shrink: 0;
  position: relative;
  z-index: 1000;
  width: 10rem;
  height: 10rem;
  background: url("/public/assets/images/logo.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  transition: 0.15s;
}
header .wrapper .menuBtn {
  position: relative;
  z-index: 1000;
  width: 3rem;
  height: 3rem;
}
@media (min-width: 1024px) {
  header .wrapper .menuBtn {
    display: none;
  }
}
header .wrapper .menuBtn * {
  position: absolute;
  width: 100%;
  height: 100%;
}
header .wrapper .menuBtn #menuBtn {
  opacity: 0;
}
header .wrapper .menuBtn #menuBtn:checked + label::before {
  transform: rotate(-45deg);
}
header .wrapper .menuBtn #menuBtn:checked + label::after {
  transform: rotate(45deg);
}
header .wrapper .menuBtn label {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
header .wrapper .menuBtn label::before, header .wrapper .menuBtn label::after {
  content: "";
  position: absolute;
  height: 0.1875rem;
  width: 60%;
  background: #fff;
  transition: 0.15s;
}
header .wrapper .menuBtn label::before {
  transform: translateY(-0.35rem);
}
header .wrapper .menuBtn label::after {
  transform: translateY(0.35rem);
}
header .wrapper nav {
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  header .wrapper nav {
    gap: 2rem;
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    flex-flow: column;
    justify-content: center;
    background: #402efe;
    transition: 0.15s;
  }
  header .wrapper nav.hidden {
    visibility: hidden;
    pointer-events: none;
    opacity: 0;
  }
}
@media (min-width: 1024px) {
  header .wrapper nav {
    width: 100%;
    justify-content: space-between;
  }
}
header .wrapper nav ul {
  display: flex;
}
@media (max-width: 1023px) {
  header .wrapper nav ul:nth-child(1) {
    display: none;
  }
  header .wrapper nav ul:nth-child(2) {
    gap: 1rem;
    flex-flow: column;
  }
}
@media (min-width: 1024px) {
  header .wrapper nav ul:nth-child(2) {
    gap: 2rem;
  }
}
header .wrapper nav ul li a {
  color: #fff;
  font-weight: 300;
