$path: "./ui/fonts/Helios/";

// 100

@font-face {
  font-family: 'Helios';
  src: url($path+'HeliosThinC.eot');
  src: local($path+'HeliosThinC'),
      url($path+'HeliosThinC.eot?#iefix') format('embedded-opentype'),
      url($path+'HeliosThinC.woff2') format('woff2'),
      url($path+'HeliosThinC.woff') format('woff'),
      url($path+'HeliosThinC.ttf') format('truetype');
  font-weight: 100;
  font-style: normal;
}

@font-face {
  font-family: 'Helios';
  src: url($path+'HeliosThinC-Italic.eot');
  src: local($path+'HeliosThinC Italic'), local($path+'HeliosThinC-Italic'),
      url($path+'HeliosThinC-Italic.eot?#iefix') format('embedded-opentype'),
      url($path+'HeliosThinC-Italic.woff2') format('woff2'),
      url($path+'HeliosThinC-Italic.woff') format('woff'),
      url($path+'HeliosThinC-Italic.ttf') format('truetype');
  font-weight: 100;
  font-style: italic;
}

